import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import 'bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css'
//fontAwesome
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { 
          faCommentsDollar, 
          faCogs,
          faTools, 
          faToolbox, 
          faPhoneAlt,
          faHome,
          faEnvelope,
          faBuilding
        } from '@fortawesome/free-solid-svg-icons'
import { 
          faWhatsapp, 
          faWhatsappSquare,
          faFacebookSquare,
          faTwitterSquare,
          faLinkedin,
          faInstagram
        } from '@fortawesome/free-brands-svg-icons'
 
library.add(
    faCommentsDollar,
    faCogs,
    faTools,
    faToolbox,
    faPhoneAlt,
    faWhatsapp,
    faWhatsappSquare,
    faFacebookSquare,
    faTwitterSquare,
    faLinkedin,
    faInstagram,
    faHome,
    faEnvelope,
    faBuilding
  )
 
Vue.component('font-awesome-icon', FontAwesomeIcon)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
